.pub{

}

.pub-title {
	font-weight: 600;
}

.pub-authors {
	margin-left: 10px;
	font-size: 0.9em;
	font-style: oblique;
}

.pub-venue {
	margin-left: 20px;
	font-size: 0.9em;
}

.pub-abstract {
	font-size: 0.8em;
	text-align: justify;
	line-height: 110%;
}

.pub-bibtex {
	font-family:"courier new";
	line-height: 110%;
	font-size: 0.8em;
}

.pub-photo{
	//display: none;
  display: block;
		width: 90px;
		margin-bottom: 10px;
		@include rounded(200px);
		@include clearfix;
    margin-left: auto;
    margin-right: auto;
	@include media($medium) {
		display: block;
		width: 150px;
		margin-bottom: 10px;
		@include rounded(200px);
		@include clearfix;
	}
}

.pub-photocol{
	width: 90px;
	display: block;
	@include media($medium) {
		display: block;
		width: 150px;
	}
}

.pub-photolink{
	text-align: center;
}

.pub-photolink img{
	width: 90px;
	height: auto;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px;
    border: 1px solid #222222;
	@include media($medium) {
		display: block;
		width: 120px;
		height:auto;
		@include clearfix;
	}
	@include media($large) {
		display: block;
		width: 140px;
		height:auto;
		@include clearfix;
	}
}

.pub-photolink td{
	width: 0px;
}

.accordion {
}

.accordion > label {
  display: block;
  color: #555555;
  font-size: 0.8em;
  text-align: right;
}

.accordion > input {
  display: none;
}

.accordion > div {
  max-height: 0;
  overflow: hidden;
}

.accordion > input:checked + label + div {
  max-height: 1000px;
}

.accordion > div > * {
  margin: 10px;
}

.accordion > div {
  margin-bottom: 1px;
  border-bottom: 1px solid #ECECEC;
  -webkit-transition: all linear 500ms;
  -moz-transition: all linear 500ms;
  -o-transition: all linear 500ms;
  transition: all linear 500ms;
}


